.button {
  background: linear-gradient(
    89.84deg,
    #1f26c5 2.77%,
    #3b26be 26.21%,
    #6026be 45.98%,
    #8224bc 70.58%,
    #8821b9 95.38%
  );
  border: 1px solid #000000;
  border-radius: 30px;
  color: white;
  padding: 15px 30px;
  font-family: "InterRegular";
  font-style: normal;
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  cursor: pointer;
}

.button:hover,
.button:active {
  border: 5px solid transparent;
  border-radius: 50px;
  padding: 10px 20px;
  background: black;
}

.button:hover {
  background: linear-gradient(#000, #000) padding-box,
    linear-gradient(
        90deg,
        rgba(226, 21, 21, 0.874275) 11.54%,
        rgba(105, 205, 44, 0.951071) 34.45%,
        rgba(218, 233, 52, 0.964639) 57.97%,
        rgba(23, 110, 214, 0.981908) 76.07%
      )
      border-box;
}

.button:active {
  background: linear-gradient(#000, #000) padding-box,
    linear-gradient(
        90deg,
        rgba(98, 0, 238, 0.951071) -7.52%,
        rgba(165, 136, 170, 0.964639) 50.74%,
        rgba(98, 0, 238, 0.981908) 108.27%
      )
      border-box;
}
