.button {
  font-family: BungeeRegular;
  background: #d8ea12;
  border: 1px solid #000000;
  border-radius: 30px;
  font-weight: 400;
  font-size: 28px;
  line-height: 34px;
  color: #431294;
  padding: 15px 30px;
  cursor: pointer;
}

.button:hover {
  border: 5px solid #26d0e8;
}

.button:hover,
.button:active {
  background: #d6e811cc;
}

.button:active {
  border: 5px solid;
  border-image-slice: 1;
  border-width: 5px;
  border-image-source: linear-gradient(
      90deg,
      #790bac 11.54%,
      #15b6a2 54.96%,
      #8122ae 108.27%
    )
    padding-box;
}
